@import url(https://fonts.googleapis.com/css2?family=Genos:ital,wght@0,300;0,400;0,500;1,400&display=swap);
.main-container {
  display: grid;
  grid-template-columns: 1fr 50rem;
  height: 100vh;
  overflow: hidden;
}


@media (max-width: 500px) {
  .main-container{
    grid-template-columns: 0fr 1fr
  }
}
.fieldset-container {
  box-shadow: -2px 0 5px #000;
  width: 100%;
  height: 100vh;
  background-color: var(--primary-color);
  box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.129);
  position: relative;
}

.fieldset-content {
  padding: 4.8rem;
}

.title h1 {
  color: white;
  font-family: var(--font-genos);
  font-size: 2rem;
}

.title p {
  color: #6c757d;
  font-family: var(--font-genos);
  font-size: 1.6rem;
  margin-bottom: 4.4rem;
  padding-top: 0.5rem;
}

.title p span {
  margin-left: 2.2rem;
  position: relative;
}

.title p span::before {
  position: absolute;
  content: "S";
  font-size: 4rem;
  left: -2.2rem;
  top: -2rem;
  font-style: italic;
}

.btn-forgetpassword {
  position: relative;
}

.btn-forgetpassword p {
  position: absolute;
  color: #ccc;
  font-style: italic;
  font-size: 1.3rem;
  right: 0;
}

.btn-forgetpassword p:hover {
  color: #aaa;
  cursor: pointer;
}

.fieldset-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 11.2rem;
  background-color: var(--third-color);
  box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.129);
}

.fieldset-footer p {
  color: white;
  font-family: var(--font-genos);
  font-size: 1.9rem;
  line-height: 6rem;
  text-align: center;
}

.info{
  position: absolute;
  top: 1rem;
  right: 1.5rem;
  color: white;
  font-size: 3rem;
  cursor: pointer;
}


.popover-div {
  position: absolute;
  top: 4rem;
  right: 4rem;
  width: 22rem;
  padding: .5rem 0;
  border-radius: 2.5rem;
  font-size: 1.8rem;
  font-style: italic;
  text-align: center;
  color: white;
  background-color: #304FFE;
  font-family: var(--font-genos);
}

.btn {
  display: block;
  width: 100%;
  border: none;
  border-radius: 4px;
  background-color: #304ffe;
  color: white;
  text-align: center;
  height: 3.6rem;
  line-height: 3rem;
  margin: 4rem 0 0 0;
  cursor: pointer;
  font-size: 1.9rem;
  font-family: var(--font-genos);
}

.btn:disabled {
  color: rgba(255, 255, 255, 0.3);
  background-color: #242424;
}

.textfield-filled {
  position: relative;
  display: inline-block;
  font-size: 1.4rem;
  overflow: hidden;
  width: 100%;
  margin-bottom: 1.8rem;
}


/* Input, Textarea */
.textfield-filled  input{
  margin: 0;
  border: none;
  border-top: solid 27px transparent;
  border-bottom: solid 1px rgba(255, 255, 255, 0.6);
  border-radius: 4px 4px 0 0;
  padding: 0 12px 14.7px;
  width: 100%;
  color: white;
  background-color: var(--input-background);
  font-family: inherit;
  font-size: inherit;
}

/* Span */
.textfield-filled span {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 7px 12px 0;
  color: var(--input-color);
  font-size: 75%;
  line-height: 18px;
  pointer-events: none;
  transition: color 0.2s, font-size 0.2s, line-height 0.2s;
}

/* Underline */
.textfield-filled span::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: rgb(33, 150, 243);
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

/* Hover */
.textfield-filled input:hover::after {
  background-color: black;
  width: 100%;
}

/* Placeholder-shown */
.textfield-filled input:not(:focus):placeholder-shown + span {
  font-size: inherit;
  line-height: 4.5rem;
}

/* Focus */
.textfield-filled input:focus {
  outline: none;
}

.textfield-filled  input:focus + span{
  color: rgb(33, 150, 243);
}

.textfield-filled input:focus + span::after {
  -webkit-transform: scale(1);
          transform: scale(1);
}

